@use "./../../styles/Shared" as shared;

main.TSP {
    article{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    aside {
        width: 100%;

        display: grid;
        grid-template-columns: 80% 20%;
        margin: 0.1em;
        align-items: center;
        
        .stored{
            display: flex;
            overflow: scroll;
            div{
                margin: 0.1em
            }
            p{
                white-space: nowrap;
                margin: 0.1em;
            }
        }

        .sidebar{
            display: flex;
            justify-content: center;
            button{
                cursor: pointer;
                border-radius: 2em;
                background-color: shared.$accent1-color;
                color: shared.$primary-color;
                border: 1px solid shared.$accent1-color;
                
                &:hover{
                    background-color: shared.$primary-color;
                    color: shared.$accent1-color;
                    border: 1px solid shared.$accent1-color;
                }
            }
        }
    }

    svg{
        max-width: 100vh;
        max-height: 80vh;
        border: 1px solid shared.$accent1-color;

        path{
            stroke: shared.$secondary-color;
        }
        circle{
            cursor: pointer;
            &.active{
                
                fill: shared.$accent1-color
            }
            &.inactive{
                fill: shared.$secondary-dark-color
            }
        }
        text{
            user-select: none;
            pointer-events:none;
        }
    }
}